body{
    background-color: rgb(245, 245, 102);
}
.table-container {
  width: 50%;
}

table {
  width: 92%;
  border-collapse: collapse;
  text-align: center;
  position: relative;
  left:6%;
  top: 34px;
}

th,td {
  padding: 8px;
  border-bottom: 1px solid #ddd;
}

th {
  background-color:rgba(17, 0, 255, 0.8);
  color:white;
   font-weight: bold;
}

tr:nth-child(even) {
  background-color: #ffb9b9;
}
tr:nth-child(odd) {
  background-color: rgb(177, 255, 141)
}
tr:hover {
  /* background-color: #e6e6e6; */
  background-color:rgba(173,216,230,0.9);
}
.button{
    background-color: #f9004d;
    color: white;
    text-decoration: none;
    border: 2px solid transparent;
    font-weight: bold;
    padding: 10px 25px;
    border-radius: 30px;
    transition: transform .4s;
}
.button:hover{
  background-color: #000;
    border: 2px solid #f9004d;
    cursor: pointer;
}